import { useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
function useModal(modalRef, done, correctDoneID) {
  const { pathname } = useLocation();
  const [doneID, setDoneID] = useState("");

  // modal
  const [modalText, setModalText] = useState([]);
  const [modalBtnText, setModalBtnText] = useState("");
  const handleClickFinish = () => {
    modalRef.current.openModal();
    if (doneID.includes("null")) {
      // 
      setModalBtnText("");
      setModalText([""]);
      return;
    }

    if (doneID === correctDoneID) {
      // 
      setModalBtnText("");

      if (pathname.includes("product_backlog_list"))
        setModalText([
          "",
          "",
          "",
        ]);
      if (pathname.includes("sprint_list"))
        setModalText([
          "",
          "",
          "",
        ]);

      if (pathname.includes("retro")) {
        setModalText([
          "",
          "",
        ]);
        setModalBtnText("");
      }
    } else {
      // 
      setModalBtnText("");
      if (pathname.includes("product_backlog_list"))
        setModalText([""]);

      if (pathname.includes("sprint_list"))
        setModalText(["", ""]);
        
      if (pathname.includes("retro"))
        setModalText([
          "",
          "",
        ]);
    }
  };
  useEffect(() => {
    let doneId =
      done && done.map((item) => (item?.id ? item?.id : "null")).join("_");
    setDoneID(doneId);
  }, [done]);

  return { modalText, modalBtnText, doneID, handleClickFinish };
}

export default useModal;
